import React from "react";
// @ts-ignore
import styles from "./ItemViewer.module.scss";
export default function ItemViewer({ item, children, onClick, ...other }) {
    if (!onClick)
        onClick = () => item.sheet.render(true);
    let image = item?.img || "icons/svg/mystery-man.svg";
    let name = item?.name || "No Item";
    return React.createElement("div", Object.assign({ className: styles.viewer, onClick: onClick }, other),
        React.createElement("img", { src: image, className: styles.image }),
        React.createElement("span", { className: styles.name }, name),
        children);
}
