import { getBuyPrice } from "./MerchantFlag";
import { MerchantBuy } from "./MerchantAction";
import { getActorId } from "../../Util/Identifiers/ActorID";
import { openItemQuantitySelect } from "../LootSheet/ItemQuantitySelector";
import React, { Fragment } from "react";
import GoldDisplay from "../../Util/Components/GoldDisplay";
import { Paper } from "@material-ui/core";
import ItemTable, { generateControlsColumn, getEditControl, ItemColumnImage, ItemColumnName, ItemColumnWeight } from "../../Util/Components/ItemTable";
import { getItemId } from "../../Util/Identifiers/ItemID";
// @ts-ignore
import Styles from "./MerchantSheet.module.scss";
function getGetQty(sellables) {
    return (item) => {
        let found = sellables.find(x => x.item === item);
        if (found) {
            return found.qty;
        }
        else {
            return item.data.data.quantity;
        }
    };
}
export default function BuySheet({ self, merchant, sellables, merchantFlag, myGoldAmount }) {
    let getQty = getGetQty(sellables);
    let buyControls = ({ item }) => {
        let controls = [getEditControl(item)];
        let found = sellables.find(x => x.item === item);
        if (item.owner && !found) {
            controls.push({
                title: "Delete",
                text: React.createElement("i", { className: "fas fa-trash" }),
                classes: "item-delete",
                onClick: () => item.delete()
            });
        }
        if (self) {
            let itemPrice = getBuyPrice(item, 1, merchantFlag);
            if (myGoldAmount >= itemPrice) {
                controls.push({
                    title: "Buy",
                    text: React.createElement("i", { className: "fas fa-hand-holding" }),
                    onClick: () => {
                        let buy = (qty) => MerchantBuy({
                            self: getActorId(self),
                            merchant: getActorId(merchant),
                            item: getItemId(item),
                            qty
                        });
                        openItemQuantitySelect({
                            item,
                            max: Math.min(getQty(item) || 10, Math.floor(myGoldAmount / itemPrice)),
                            buttonText: (qty) => React.createElement(Fragment, null,
                                "Buy",
                                React.createElement(GoldDisplay, { value: getBuyPrice(item, qty, merchantFlag) })),
                            onConfirm: buy
                        });
                    }
                });
            }
        }
        return controls;
    };
    let columns = [
        ItemColumnImage,
        ItemColumnName,
        ItemColumnWeight,
        {
            title: "Qty",
            getter: ({ item }) => getQty(item)
        },
        {
            title: "Price",
            getter: ({ item }) => React.createElement(GoldDisplay, { value: getBuyPrice(item, 1, merchantFlag) })
        },
        {
            title: "Value",
            getter: ({ item }) => React.createElement(GoldDisplay, { value: item.data.data.price })
        },
        generateControlsColumn(buyControls)
    ];
    return React.createElement(Paper, { classes: { root: Styles.paperDiv } },
        "Buy:",
        React.createElement(ItemTable, { items: [...merchant.items.entries, ...sellables.map(x => x.item)], columns: columns }));
}
